@import "../../style/mixins";

$confirm-prefix-cls: "#{$css-prefix}confirm";

.#{$confirm-prefix-cls} {
  .#{$css-prefix}modal-header {
    display: none;
  }

  .#{$css-prefix}modal-close {
    display: none;
  }

  .#{$css-prefix}modal-body {
    padding: 30px 40px;
  }

  &-body-wrapper {
    @include clearfix();
  }

  &-body {
    .#{$confirm-prefix-cls}-title {
      color: $text-color;
      font-weight: bold;
      font-size: $font-size-lg;
    }

    .#{$confirm-prefix-cls}-content {
      margin-left: 42px;
      font-size: $font-size-base;
      color: $text-color;
      margin-top: 8px;
    }

    > .#{$iconfont-css-prefix} {
      font-size: 24px;
      margin-right: 16px;
      padding: 0 1px;
      float: left;
    }
  }

  .#{$confirm-prefix-cls}-btns {
    margin-top: 30px;
    float: right;

    button + button {
      margin-left: 10px;
      margin-bottom: 0;
    }
  }

  &-error &-body > .#{$iconfont-css-prefix} {
    color: $error-color;
  }

  &-warning &-body > .#{$iconfont-css-prefix},
  &-confirm &-body > .#{$iconfont-css-prefix} {
    color: $warning-color;
  }

  &-info &-body > .#{$iconfont-css-prefix} {
    color: $primary-color;
  }

  &-success &-body > .#{$iconfont-css-prefix} {
    color: $success-color;
  }
}



