@import "../../style/config";
@import "../../style/mixins";

$menu-prefix-cls: "#{$css-prefix}menu";

// default theme
.#{$menu-prefix-cls} {
  outline: none;
  margin-bottom: 0;
  padding-left: 0; // Override default ul/ol
  list-style: none;
  z-index: $zindex-dropdown;
  box-shadow: $box-shadow-base;
  color: $text-color;
  background: #fff;
  line-height: 46px;

  &-hidden {
    display: none;
  }

  &-item-group-list {
    margin: 0;
    padding: 0;
  }

  &-item-group-title {
    color: $text-color-secondary;
    font-size: $font-size-base;
    line-height: 1.5;
    padding: 8px 16px;
  }

  &-item,
  &-submenu,
  &-submenu-title {
    cursor: pointer;
    transition: all 0.3s ease;
  }

  &-submenu &-sub {
    cursor: initial;
  }

  &-item > a {
    display: block;
    color: $text-color;
    &:hover {
      color: $text-color;
    }
    &:before {
      position: absolute;
      background-color: transparent;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      content: '';
    }
  }

  &-item:hover,
  &-item-active,
  &-submenu-active,
  &-submenu-title:hover {
    background-color: tint($primary-color, 90%);
  }

  &-horizontal &-item,
  &-horizontal &-submenu {
    margin-top: -1px;
  }

  &-horizontal > &-item:hover,
  &-horizontal > &-item-active,
  &-horizontal > &-submenu &-submenu-title:hover {
    background-color: transparent;
  }

  &-item-selected {
    color: $primary-color;
    // fix chrome render bug
    transform: translateZ(0);
    > a,
    > a:hover {
      color: $primary-color;
    }
  }

  &-horizontal,
  &-inline,
  &-vertical {
    z-index: auto;
  }

  &-inline,
  &-vertical {
    border-right: 1px solid $border-color-split;
    .#{$menu-prefix-cls}-item {
      border-right: 1px solid $border-color-split;
      margin-left: -1px;
      left: 1px;
      position: relative;
      z-index: 1;
    }
  }

  &-vertical &-sub {
    border-right: 0;
    .#{$menu-prefix-cls}-item {
      border-right: 0;
    }
  }

  &-inline {
    .#{$menu-prefix-cls}-selected,
    .#{$menu-prefix-cls}-item-selected {
      border-right: 2px solid $primary-color;
      transform: translateZ(0);
    }
  }

  &-submenu-horizontal > & {
    top: 100%;
    left: 0;
    position: absolute;
    min-width: 100%;
    margin-top: 7px;
    z-index: $zindex-dropdown;
  }

  &-submenu-vertical {
    z-index: 1;
  }

  &-submenu-vertical > & {
    top: 0;
    left: 100%;
    position: absolute;
    min-width: 160px;
    margin-left: 4px;
    z-index: $zindex-dropdown;
  }

  &-item,
  &-submenu-title {
    margin: 0;
    padding: 0 20px;
    position: relative;
    display: block;
    white-space: nowrap;

    // Disabled state sets text to gray and nukes hover/tab effects
    &.#{$menu-prefix-cls}-item-disabled,
    &.#{$menu-prefix-cls}-submenu-disabled {
      color: $text-color-secondary !important;
      cursor: not-allowed;
      background: none;
      > a {
        color: $text-color-secondary !important;
        pointer-events: none;
      }
    }

    .#{$iconfont-css-prefix} {
      min-width: 14px;
      margin-right: 8px;
    }
  }

  & > &-item-divider {
    height: 1px;
    margin: 1px 0;
    overflow: hidden;
    padding: 0;
    line-height: 0;
    background-color: #e5e5e5;
  }

  &-submenu {
    position: relative;

    > .#{$menu-prefix-cls} {
      background-color: #fff;
      border-radius: 4px;
    }

    &-vertical > .#{$menu-prefix-cls}-submenu-title:after {
      font-family: "anticon" !important;
      font-style: normal;
      vertical-align: baseline;
      text-align: center;
      text-transform: none;
      text-rendering: auto;
      position: absolute;
      transition: transform .3s ease;
      content: "\e61d";
      right: 16px;
      transform: rotate(270deg) scale(0.75);
    }

    &-inline > .#{$menu-prefix-cls}-submenu-title:after {
      font-family: "anticon" !important;
      font-style: normal;
      vertical-align: baseline;
      text-align: center;
      text-transform: none;
      text-rendering: auto;
      position: absolute;
      transition: transform .3s ease;
      content: "\e61d";
      right: 16px;
      top: 0;
      @include iconfont-size-under-12px(8px);
    }

    &-open {
      &.#{$menu-prefix-cls}-submenu-inline > .#{$menu-prefix-cls}-submenu-title:after {
        transform: rotate(180deg) scale(0.75);
      }
    }
  }

  &-vertical &-submenu-selected {
    color: $primary-color;
    > a {
      color: $primary-color;
    }
  }

  &-horizontal {
    border: 0;
    border-bottom: 1px solid $border-color-split;
    box-shadow: none;
    z-index: 0;

    > .#{$menu-prefix-cls}-item,
    > .#{$menu-prefix-cls}-submenu {
      position: relative;
      top: 1px;
      float: left;
      border-bottom: 2px solid transparent;

      &:hover,
      &-active,
      &-selected {
        border-bottom: 2px solid $primary-color;
        color: $primary-color;
      }

      > a {
        display: block;
        color: $text-color;
        &:hover {
          color: $primary-color;
        }
      }
    }

    &:after {
      content: "\20";
      display: block;
      height: 0;
      clear: both;
    }
  }

  &-vertical,
  &-inline,
  &-item-group-list {
    & > .#{$menu-prefix-cls}-item,
    & > .#{$menu-prefix-cls}-submenu > .#{$menu-prefix-cls}-submenu-title {
      padding: 0px 16px 0 28px;
      font-size: $font-size-base;
      line-height: 42px;
      height: 42px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &-vertical.#{$menu-prefix-cls}-sub {
    padding: 0;
    transform-origin: 0 0;
    & > .#{$menu-prefix-cls}-item,
    & > .#{$menu-prefix-cls}-submenu {
      transform-origin: 0 0;
    }
  }

  &-root.#{$menu-prefix-cls}-vertical,
  &-root.#{$menu-prefix-cls}-inline {
    box-shadow: none;
  }

  &-sub.#{$menu-prefix-cls}-inline {
    padding: 0;
    border: 0;
    box-shadow: none;
    border-radius: 0;
    & > .#{$menu-prefix-cls}-item,
    & > .#{$menu-prefix-cls}-submenu > .#{$menu-prefix-cls}-submenu-title {
      line-height: 42px;
      height: 42px;
      list-style-type: disc;
      list-style-position: inside;
    }

    & .#{$menu-prefix-cls}-item-group-title {
      padding-left: 32px;
    }
  }
}


// dark theme
.#{$menu-prefix-cls} {
  &-dark,
  &-dark &-sub {
    color: $text-color-secondary;
    background: #404040;
  }

  &-dark &-inline.#{$menu-prefix-cls}-sub {
    background: #333;
  }

  &-dark.#{$menu-prefix-cls}-horizontal {
    border-bottom-color: #404040;
  }

  &-dark.#{$menu-prefix-cls}-horizontal > &-item,
  &-dark.#{$menu-prefix-cls}-horizontal > &-submenu {
    border-color: #404040;
    border-bottom: 0;
    top: 0;
  }

  &-dark &-item,
  &-dark &-item > a {
    color: $text-color-secondary;
  }

  &-dark.#{$menu-prefix-cls}-inline,
  &-dark.#{$menu-prefix-cls}-vertical {
    border-right: 0;
  }

  &-dark.#{$menu-prefix-cls}-inline &-item,
  &-dark.#{$menu-prefix-cls}-vertical &-item {
    border-right: 0;
    margin-left: 0;
    left: 0;
  }

  &-dark &-item:hover,
  &-dark &-item-active,
  &-dark &-submenu-active,
  &-dark &-submenu-selected,
  &-dark &-submenu:hover,
  &-dark &-submenu-title:hover {
    background-color: transparent;
    color: #fff;
    > a {
      color: #fff;
    }
  }

  &-dark &-item-selected {
    border-right: 0;
    color: #fff;
    > a,
    > a:hover {
      color: #fff;
    }
  }

  &-dark.#{$menu-prefix-cls}-inline &-item-selected {
    background-color: $primary-color;
  }
}



