@import "../../style/config";
@import "../../style/mixins";

$dropdown-prefix-cls: "#{$css-prefix}dropdown";

.#{$dropdown-prefix-cls} {
  position: absolute;
  left: -9999px;
  top: -9999px;
  z-index: $zindex-dropdown;
  display: block;
  font-size: $font-size-base;
  font-weight: normal;
  line-height: 1.5;

  &-wrap {
    position: relative;

    .#{$css-prefix}btn > .#{$iconfont-css-prefix}-down {
      @include iconfont-size-under-12px(9px);
    }

    .#{$iconfont-css-prefix}-down:before {
      transition: transform 0.2s ease;
    }
  }

  &-wrap-open {
    .#{$iconfont-css-prefix}-down:before {
      transform: rotate(180deg);
    }
  }

  &-hidden,
  &-menu-hidden {
    display: none;
  }

  &-menu {
    outline: none;
    position: relative;
    list-style-type: none;
    padding: 0;
    margin: 0;
    text-align: left;
    background-color: #fff;
    border-radius: $border-radius-base;
    box-shadow: $box-shadow-base;
    background-clip: padding-box;

    &-item,
    &-submenu-title {
      padding: 7px 16px;
      margin: 0;
      clear: both;
      font-size: $font-size-base;
      font-weight: normal;
      color: $text-color;
      white-space: nowrap;
      cursor: pointer;
      transition: background 0.3s ease;

      > a {
        color: $text-color;
        display: block;
        padding: 7px 16px;
        margin: -7px -16px;
      }

      &:hover {
        background-color: tint($primary-color, 90%);
      }

      &-disabled {
        color: #ccc;
        cursor: not-allowed;
        pointer-events: none;

        &:hover {
          color: #ccc;
          background-color: #fff;
          cursor: not-allowed;
        }
      }

      &:first-child {
        border-radius: $border-radius-base $border-radius-base 0 0;
      }

      &:last-child {
        border-radius: 0 0 $border-radius-base $border-radius-base;
      }

      &:only-child {
        border-radius: $border-radius-base;
      }

      &-divider {
        height: 1px;
        overflow: hidden;
        background-color: $border-color-split;
        line-height: 0;
      }
    }

    &-submenu-title:after {
      font-family: "anticon" !important;
      position: absolute;
      content: "\e61f";
      right: 16px;
      color: $text-color-secondary;
      @include iconfont-size-under-12px(9px);
    }

    &-submenu-vertical {
      position: relative;
    }

    &-submenu-vertical > & {
      top: 0;
      left: 100%;
      position: absolute;
      min-width: 100%;
      margin-left: 4px;
      transform-origin: 0 0;
    }

    &-submenu:first-child &-submenu-title {
      border-radius: $border-radius-base $border-radius-base 0 0;
    }

    &-submenu:last-child &-submenu-title {
      border-radius: 0 0 $border-radius-base $border-radius-base;
    }
  }

  &.slide-up-enter.slide-up-enter-active#{$dropdown-prefix-cls}-placement-bottomLeft,
  &.slide-up-appear.slide-up-appear-active#{$dropdown-prefix-cls}-placement-bottomLeft {
    animation-name: antSlideUpIn;
  }

  &.slide-up-enter.slide-up-enter-active#{$dropdown-prefix-cls}-placement-topLeft,
  &.slide-up-appear.slide-up-appear-active#{$dropdown-prefix-cls}-placement-topLeft {
    animation-name: antSlideDownIn;
  }

  &.slide-up-leave.slide-up-leave-active#{$dropdown-prefix-cls}-placement-bottomLeft {
    animation-name: antSlideUpOut;
  }

  &.slide-up-leave.slide-up-leave-active#{$dropdown-prefix-cls}-placement-topLeft {
    animation-name: antSlideDownOut;
  }
}

.#{$dropdown-prefix-cls}-link {
  font-size: $font-size-base;
  .#{$iconfont-css-prefix}-down {
    @include iconfont-size-under-12px(9px);
  }
}

.#{$dropdown-prefix-cls}-button {
  &.#{$css-prefix}btn-group > .#{$css-prefix}btn:last-child:not(:first-child) {
    padding-right: 7px;
  }
  .#{$iconfont-css-prefix}-down {
    @include iconfont-size-under-12px(9px);
  }
}

.#{$css-prefix}btn .anticon-down {
  @include iconfont-size-under-12px(9px);
}



