@import "../../style/config";
@import "../../style/mixins";

$table-prefix-cls: "#{$css-prefix}table";
$table-head-background-color: $background-color-base;

.#{$table-prefix-cls} {
  font-size: $font-size-base;
  color: $text-color;
  overflow: hidden;
  position: relative;
  border-radius: $border-radius-base $border-radius-base 0 0;
  overflow: hidden;

  &-body {
    transition: opacity 0.3s ease;
  }

  table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    text-align: left;
    border-radius: $border-radius-base $border-radius-base 0 0;
    overflow: hidden;
  }

  &-thead > tr > th {
    background: $table-head-background-color;
    font-weight: bold;
    transition: background .3s ease;
    text-align: left;

    .#{$iconfont-css-prefix}-filter {
      margin-left: 4px;
      font-size: $font-size-base;
      cursor: pointer;
      color: #aaa;
      transition: all 0.3s ease;
      &:hover {
        color: $text-color;
      }
    }

    .#{$table-prefix-cls}-filter-selected.#{$iconfont-css-prefix}-filter {
      color: $primary-color;
    }
  }

  &-tbody > tr > td {
    border-bottom: 1px solid $border-color-split;
    position: relative;
  }

  &-thead > tr,
  &-tbody > tr {
    transition: all .3s ease;
    &.#{$table-prefix-cls}-row-hover,
    &:hover {
      background: tint($primary-color, 90%);
    }
  }

  &-thead > tr:hover {
    background: none;
  }

  &-footer {
    padding: 16px 8px;
    background: $table-head-background-color;
    position: relative;
    z-index: 2;
    top: -1px;
    border-radius: 0 0 $border-radius-base $border-radius-base;
  }

  &.#{$table-prefix-cls}-bordered &-footer {
    border: 1px solid $border-color-split;
  }

  &-title {
    padding: 16px 8px;
    position: relative;
    top: 1px;
    border-radius: $border-radius-base $border-radius-base 0 0;
  }

  &.#{$table-prefix-cls}-bordered &-title {
    border: 1px solid $border-color-split;
  }

  &-title + &-content {
    position: relative;

    table {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }

  &-tbody > tr.#{$table-prefix-cls}-row-selected {
    background: #fafafa;
  }

  &-thead > tr > th.#{$table-prefix-cls}-column-sort {
    background: #eaeaea;
  }

  &-thead > tr > th,
  &-tbody > tr > td {
    padding: 16px 8px;
    word-break: break-all;
  }

  &-thead > tr > th.#{$table-prefix-cls}-selection-column,
  &-tbody > tr > td.#{$table-prefix-cls}-selection-column {
    text-align: center;
    width: 60px;
  }

  &-header {
    background: $table-head-background-color;
    overflow: hidden;
  }

  &-header table {
    border-radius: $border-radius-base $border-radius-base 0 0;
  }

  &-loading {
    position: relative;
    .#{$table-prefix-cls}-body {
      background: #fff;
      opacity: 0.5;
    }
    .#{$table-prefix-cls}-spin-holder {
      height: 20px;
      line-height: 20px;
      left: 50%;
      top: 50%;
      margin-left: -30px;
      position: absolute;
    }
    .#{$table-prefix-cls}-with-pagination {
      margin-top: -20px;
    }
    .#{$table-prefix-cls}-without-pagination {
      margin-top: 10px;
    }
  }

  &-middle {
    .#{$table-prefix-cls}-thead > tr > th,
    .#{$table-prefix-cls}-tbody > tr > td {
      padding: 10px 8px;
    }
  }

  &-small {
    border: 1px solid $border-color-split;
    border-radius: $border-radius-base;

    .#{$table-prefix-cls}-header > table,
    .#{$table-prefix-cls}-body > table {
      border: 0;
      padding: 0 8px;
    }

    .#{$table-prefix-cls}-thead > tr > th {
      padding: 10px 8px;
      background: #fff;
      border-bottom: 1px solid $border-color-split;
    }

    .#{$table-prefix-cls}-tbody > tr > td {
      padding: 6px 8px;
    }

    .#{$table-prefix-cls}-header {
      background: #fff;
      table {
        border-bottom: 1px solid $border-color-split;
      }
      .#{$table-prefix-cls}-thead > tr > th {
        border-bottom: 0;
      }
    }

    .#{$table-prefix-cls}-row:last-child td {
      border-bottom: 0;
    }

    .#{$table-prefix-cls}-title,
    .#{$table-prefix-cls}-footer {
      top: 0;
    }
  }

  &-column-sorter {
    margin-left: 4px;
    display: inline-block;
    width: 12px;
    height: 14px;
    vertical-align: middle;
    text-align: center;
    &-up,
    &-down {
      line-height: 4px;
      height: 5px;
      display: block;
      width: 12px;
      cursor: pointer;
      &:hover .#{$iconfont-css-prefix} {
        color: $text-color;
      }
      &.on {
        .#{$iconfont-css-prefix}-caret-up,
        .#{$iconfont-css-prefix}-caret-down {
          color: $primary-color;
        }
      }
    }
    .#{$iconfont-css-prefix}-caret-up,
    .#{$iconfont-css-prefix}-caret-down {
      @include iconfont-size-under-12px(7px);
      line-height: 6px;
      height: 6px;
      color: #aaa;
      &:before {
        -moz-transform-origin: 53% 50%; /* fix firefox position */
      }
    }
  }

  &-bordered {
    .#{$table-prefix-cls}-header > table,
    .#{$table-prefix-cls}-body > table,
    .#{$table-prefix-cls}-fixed-left table,
    .#{$table-prefix-cls}-fixed-right table {
      border: 1px solid $border-color-split;
      border-right: 0;
      border-bottom: 0;
    }

    &.#{$table-prefix-cls}-fixed-header {
      .#{$table-prefix-cls}-header > table {
        border-bottom: 0;
      }

      .#{$table-prefix-cls}-body > table {
        border-top: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }

      .#{$table-prefix-cls}-body-inner > table {
        border-top: 0;
      }

      .#{$table-prefix-cls}-placeholder {
        border-bottom: 0;
      }
    }

    .#{$table-prefix-cls}-thead > tr > th {
      border-bottom: 1px solid $border-color-split;
    }

    &.#{$table-prefix-cls}-empty .#{$table-prefix-cls}-thead > tr > th {
      border-bottom: 0;
    }

    .#{$table-prefix-cls}-thead > tr > th,
    .#{$table-prefix-cls}-tbody > tr > td {
      border-right: 1px solid $border-color-split;
    }

    &.#{$table-prefix-cls}-small {
      .#{$table-prefix-cls}-header > table,
      .#{$table-prefix-cls}-body > table,
      .#{$table-prefix-cls}-fixed-left table,
      .#{$table-prefix-cls}-fixed-right table {
        border: 0;
      }

      .#{$table-prefix-cls}-title {
        border: 0;
        border-bottom: 1px solid $border-color-split;
      }

      .#{$table-prefix-cls}-footer {
        border: 0;
        border-top: 1px solid $border-color-split;
      }
    }
  }

  &-placeholder {
    padding: 16px 8px;
    background: #fff;
    border-bottom: 1px solid $border-color-split;;
    text-align: center;
    position: relative;
    z-index: 2;
    font-size: $font-size-base;
    color: $text-color-secondary;
    .#{$iconfont-css-prefix} {
      margin-right: 4px;
    }
  }

  &-pagination {
    margin: 16px 0;
    float: right;
  }

  &-filter-dropdown {
    min-width: 96px;
    margin-left: -8px;
    background: #fff;
    border-radius: $border-radius-base;
    box-shadow: $box-shadow-base;

    .#{$css-prefix}dropdown-menu {
      border: 0;
      box-shadow: none;
      border-radius: $border-radius-base $border-radius-base 0 0;

      &-item > label + span {
        margin-left: 8px;
      }

      &-sub {
        border-radius: $border-radius-base;
        box-shadow: $box-shadow-base;
      }

      .#{$css-prefix}dropdown-submenu-contain-selected {
        .#{$css-prefix}dropdown-menu-submenu-title:after {
          color: $primary-color;
          font-weight: bold;
          text-shadow: 0 0 2px tint($primary-color, 80%);
        }
      }
    }

    .#{$css-prefix}dropdown-menu-item {
      overflow: hidden;
    }

    > .#{$css-prefix}dropdown-menu > .#{$css-prefix}dropdown-menu-item:last-child,
    > .#{$css-prefix}dropdown-menu > .#{$css-prefix}dropdown-menu-submenu:last-child .#{$css-prefix}dropdown-menu-submenu-title {
      border-radius: 0;
    }

    &-btns {
      overflow: hidden;
      padding: 7px 16px;
      border-top: 1px solid $border-color-split;
    }

    &-link {
      color: $link-color;
      &:hover {
        color: $link-hover-color;
      }
      &:active {
        color: $link-active-color;
      }
      &.confirm {
        float: left;
      }
      &.clear {
        float: right;
      }
    }
  }

  &-expand-icon-th {
    width: 34px;
  }

  &-row {
    &-expand-icon {
      cursor: pointer;
      display: inline-block;
      width: 17px;
      height: 17px;
      text-align: center;
      line-height: 14px;
      border: 1px solid $border-color-split;
      user-select: none;
      background: #fff;
      &-cell {
        width: 18px;
      }
    }

    &-expanded:after {
      content: '-';
    }

    &-collapsed:after {
      content: '+';
    }

    &-spaced {
      visibility: hidden;
      &:after {
        content: '.';
      }
    }

    &[class*="#{$table-prefix-cls}-row-level-0"] .#{$table-prefix-cls}-selection-column > span {
      display: inline-block;
    }
  }

  tr.#{$table-prefix-cls}-expanded-row {
    &,
    &:hover {
      background: #fbfbfb;
    }
  }

  .#{$table-prefix-cls}-row-indent + .#{$table-prefix-cls}-row-expand-icon {
    margin-right: 8px;
  }

  &-scroll {
    overflow: auto;
    table {
      width: auto;
      min-width: 100%;
    }
  }

  &-body-inner {
    height: 100%;
  }

  &-fixed-header &-body {
    position: relative;
    background: #fff;
  }

  &-fixed-header &-body-inner {
    overflow: scroll;
  }

  &-fixed-header &-scroll &-header {
    overflow: scroll;
    padding-bottom: 20px;
    margin-bottom: -20px;
  }

  /* fix firefox scrollbar bug */
  &-fixed-header.#{$table-prefix-cls}-empty &-scroll &-body {
    padding-bottom: 20px;
    margin-bottom: -20px;
  }

  &-fixed-left,
  &-fixed-right {
    position: absolute;
    top: 0;
    overflow: hidden;
    z-index: 1;
    transition: box-shadow .3s ease;
    border-radius: 0;
    table {
      width: auto;
      background: #fff;
    }
  }

  &-fixed-header &-fixed-left &-body-outer &-fixed,
  &-fixed-header &-fixed-right &-body-outer &-fixed {
    border-radius: 0;
  }

  &-fixed-left {
    left: 0;
    box-shadow: $shadow-1-right;
    .#{$table-prefix-cls}-header {
      overflow-y: hidden;
    }
    // hide scrollbar in left fixed columns
    .#{$table-prefix-cls}-body-inner {
      margin-right: -20px;
      padding-right: 20px;
    }
    .#{$table-prefix-cls}-fixed-header & .#{$table-prefix-cls}-body-inner {
      padding-right: 0;
    }
    &,
    table {
      border-radius: $border-radius-base 0 0 0;
    }
  }

  &-fixed-right {
    right: 0;
    box-shadow: $shadow-1-left;
    &,
    table {
      border-radius: 0 $border-radius-base 0 0;
    }
    // hide expand row content in right-fixed Table
    // https://github.com/ant-design/ant-design/issues/1898
    .#{$table-prefix-cls}-expanded-row {
      color: transparent;
      pointer-events: none;
    }
  }

  &-scroll-position-left &-fixed-left {
    box-shadow: none;
  }

  &-scroll-position-right &-fixed-right {
    box-shadow: none;
  }
}



