@import "../../style/config";
@import "../../style/mixins";

$collapse-prefix-cls: "#{$css-prefix}collapse";

@mixin collapse-close() {
  @include iconfont-size-under-12px(7px, 270deg);
}
@mixin collapse-open() {
  @include iconfont-size-under-12px(7px, 360deg);
}

.#{$collapse-prefix-cls} {
  background-color: $background-color-base;
  border-radius: 3px;
  border: 1px solid $border-color-base;

  & > &-item {
    border-top: 1px solid $border-color-base;
    &:first-child {
      border-top: 0;
    }

    > .#{$collapse-prefix-cls}-header {
      height: 38px;
      line-height: 38px;
      padding-left: 32px;
      color: $text-color;
      cursor: pointer;
      position: relative;

      .arrow {
        @include collapse-close();
        @include iconfont-mixin();
        position: absolute;
        color: $text-color;
        display: inline-block;
        margin-right: 8px;
        line-height: 40px;
        content: "\e606";
        vertical-align: middle;
        transition: transform 0.24s ease;
        top: 0;
        left: 16px;
        &:before {
          content: "\e606";
        }
      }
    }
  }

  &-anim-active {
    transition: height .2s $ease-out;
  }

  &-content {
    overflow: hidden;
    color: $text-color;
    padding: 0 16px;
    background-color: #fff;

    & > &-box {
      padding-top: 16px;
      padding-bottom: 16px;
    }

    &-inactive {
      display: none;
    }
  }

  &-item:last-child {
    > .#{$collapse-prefix-cls}-content {
      border-radius: 0 0 3px 3px;
    }
  }

  & > &-item > &-header[aria-expanded="true"] {
    .arrow {
      @include collapse-open();
    }
  }

  &-borderless {
    background-color: #fff;
    border: 0;
  }

  &-borderless > &-item {
    border: 0;
  }

  &-borderless > &-item > &-header {
    border-bottom: 1px solid $border-color-base;
    transition: all .3s;
    border-radius: $border-color-base $border-color-base 0 0;
    &:hover {
      background-color: #fcfcfc;
    }
  }
}



