@import "../../style/config";
@import "../../style/mixins";
@import "../../input/style/mixin";

$timepicker-prefix-cls: "#{$css-prefix}time-picker";

.#{$timepicker-prefix-cls}-panel {
  max-width: 168px;
  z-index: $zindex-picker;
  position: absolute;

  &-inner {
    display: inline-block;
    position: relative;
    outline: none;
    list-style: none;
    font-size: $font-size-base;
    text-align: left;
    background-color: #fff;
    border-radius: $border-radius-base;
    box-shadow: $box-shadow-base;
    background-clip: padding-box;
    line-height: 1.5;
    overflow: hidden;
    left: -2px;
  }

  &-input {
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    cursor: auto;
    line-height: 1.5;
    outline: 0;

    &-wrap {
      box-sizing: border-box;
      position: relative;
      padding: 6px;
      border-bottom: 1px solid $border-color-split;
    }

    &-invalid {
      border-color: red;
    }
  }

  &-clear-btn {
    position: absolute;
    right: 5px;
    cursor: pointer;
    overflow: hidden;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    top: 5px;
    margin: 0;
  }

  &-clear-btn:after {
    font-size: $font-size-base;
    color: #ccc;
    display: inline-block;
    line-height: 1;
    width: 20px;
    transition: color 0.3s ease;
    @include iconfont-font("e62e");
  }

  &-clear-btn:hover:after {
    color: $text-color-secondary;
  }

  &-narrow &-input-wrap {
    max-width: 111px;
  }

  &-select {
    float: left;
    font-size: $font-size-base;
    border-left: 1px solid $border-color-split;
    box-sizing: border-box;
    width: 56px;
    overflow: hidden;
    position: relative; // Fix chrome weird render bug
    max-height: 144px;

    &:hover {
      overflow-y: auto;
    }

    &:first-child {
      border-left: 0;
      margin-left: 0;
    }

    &:last-child {
      border-right: 0;
    }

    &:only-child {
      width: 100%;
    }

    ul {
      list-style: none;
      box-sizing: border-box;
      margin: 0;
      padding: 0 0 120px 0;
      width: 100%;
    }

    li {
      list-style: none;
      box-sizing: content-box;
      margin: 0;
      padding: 0 0 0 16px;
      width: 100%;
      height: 24px;
      line-height: 24px;
      text-align: left;
      cursor: pointer;
      user-select: none;
      transition: background 0.3s ease;
    }

    li:hover {
      background: tint($primary-color, 90%);
    }

    li.#{$timepicker-prefix-cls}-panel-option-selected {
      background: $background-color-base;
      font-weight: bold;
    }

    li.#{$timepicker-prefix-cls}-panel-option-disabled {
      color: $btn-disable-color;
      &:hover {
        background: transparent;
        cursor: not-allowed;
      }
    }
  }

  &-combobox {
    @include clearfix;
  }

  &-addon {
    padding: 8px;
    border-top: 1px solid $border-color-split;
  }

  &.slide-up-enter.slide-up-enter-active.#{$timepicker-prefix-cls}-panel-placement-topLeft,
  &.slide-up-enter.slide-up-enter-active.#{$timepicker-prefix-cls}-panel-placement-topRight,
  &.slide-up-appear.slide-up-appear-active.#{$timepicker-prefix-cls}-panel-placement-topLeft,
  &.slide-up-appear.slide-up-appear-active.#{$timepicker-prefix-cls}-panel-placement-topRight {
    animation-name: antSlideDownIn;
  }

  &.slide-up-enter.slide-up-enter-active.#{$timepicker-prefix-cls}-panel-placement-bottomLeft,
  &.slide-up-enter.slide-up-enter-active.#{$timepicker-prefix-cls}-panel-placement-bottomRight,
  &.slide-up-appear.slide-up-appear-active.#{$timepicker-prefix-cls}-panel-placement-bottomLeft,
  &.slide-up-appear.slide-up-appear-active.#{$timepicker-prefix-cls}-panel-placement-bottomRight {
    animation-name: antSlideUpIn;
  }

  &.slide-up-leave.slide-up-leave-active.#{$timepicker-prefix-cls}-panel-placement-topLeft,
  &.slide-up-leave.slide-up-leave-active.#{$timepicker-prefix-cls}-panel-placement-topRight {
    animation-name: antSlideDownOut;
  }

  &.slide-up-leave.slide-up-leave-active.#{$timepicker-prefix-cls}-panel-placement-bottomLeft,
  &.slide-up-leave.slide-up-leave-active.#{$timepicker-prefix-cls}-panel-placement-bottomRight {
    animation-name: antSlideUpOut;
  }
}

.#{$timepicker-prefix-cls} {
  position: relative;
  display: inline-block;
  outline: none;
  font-size: $font-size-base;
  transition: opacity 0.3s ease;

  &-input {
    @include input;
    width: 100px;
  }

  &-large &-input {
    @include input-lg;
  }

  &-small &-input {
    @include input-sm;
  }

  &-open {
    opacity: 0;
  }

  &-icon {
    position: absolute;
    user-select: none;
    transition: all .3s $ease-in-out;
    width: 12px;
    height: 12px;
    line-height: 12px;
    right: 8px;
    color: $text-color-secondary;
    top: 50%;
    margin-top: -6px;
    &:after {
      content: "\e641";
      font-family: "anticon";
      font-size: $font-size-base;
      color: $text-color-secondary;
      display: inline-block;
      line-height: 1;
      vertical-align: bottom;
    }
  }
}



