@import "../../style/config";
@import "../../style/mixins";

$slider-prefix-cls: "#{$css-prefix}slider";
// slider color
$slider-disabled-color: #ccc;
// tooltip
$slider-tooltip-color: #fff;
$slider-tooltip-bg: tint($text-color, 4%);
$slider-tooltip-arrow-width: 4px;
$slider-tooltip-distance: $slider-tooltip-arrow-width + 4;
$slider-tooltip-arrow-color: $slider-tooltip-bg;

.#{$slider-prefix-cls} {
  position: relative;
  margin: 10px 8px;
  height: 12px;
  border-radius: 5px;
  background-color: #e9e9e9;
  cursor: pointer;
  border-top: 4px solid #fff;
  border-bottom: 4px solid #fff;
  transition: background-color 0.3s ease;

  &-track {
    position: absolute;
    left: 0;
    height: 4px;
    border-radius: $border-radius-base;
    background-color: tint($primary-color, 60%);
    z-index: 1;
    transition: background-color 0.3s ease;
  }

  &:hover {
    background-color: #e1e1e1;
    .#{$slider-prefix-cls}-handle {
      border-color: tint($primary-color, 20%);
    }
  }

  &:hover &-track {
    background-color: tint($primary-color, 40%);
  }

  &-handle {
    position: absolute;
    margin-left: -7px;
    margin-top: -5px;
    width: 14px;
    height: 14px;
    cursor: pointer;
    border-radius: 50%;
    border: solid 2px tint($primary-color, 50%);
    background-color: #fff;
    z-index: 2;
    transition: border-color 0.3s ease, transform .3s cubic-bezier(0.18, 0.89, 0.32, 1.28);

    &:hover {
      border-color: tint($primary-color, 20%);
      transform: scale(1.2);
      transform-origin: center center;
    }

    &:active {
      box-shadow: 0 0 0 2px fadeout($primary-color, 80%);
    }
  }

  &-mark {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    font-size: $font-size-base;
    z-index: 3;
  }

  &-mark-text {
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    color: $text-color-secondary;

    &-active {
      color: $text-color;
    }
  }

  &-step {
    position: absolute;
    width: 100%;
    height: 4px;
    background: transparent;
    z-index: 1;
  }

  &-dot {
    position: absolute;
    top: -2px;
    margin-left: -4px;
    width: 8px;
    height: 8px;
    border: 2px solid #e9e9e9;
    background-color: #fff;
    cursor: pointer;
    border-radius: 50%;
    vertical-align: middle;
    &:first-child {
      margin-left: -4px;
    }
    &:last-child {
      margin-left: -4px;
    }
    &-active {
      border-color: tint($primary-color, 50%);
    }
  }

  &-disabled {
    background-color: #e9e9e9 !important;

    .#{$slider-prefix-cls}-track {
      background-color: $slider-disabled-color !important;
    }

    .#{$slider-prefix-cls}-handle,
    .#{$slider-prefix-cls}-dot {
      border-color: $slider-disabled-color!important;
      background-color: #fff;
      cursor: not-allowed;
    }

    .#{$slider-prefix-cls}-mark-text,
    .#{$slider-prefix-cls}-dot {
      cursor: not-allowed!important;
    }
  }
}



