@import "../../input/style/mixin";

@mixin form-control-validation($text-color: $input-color, $border-color: $input-border-color, $background-color: $input-bg) {
  .#{$css-prefix}form-explain,
  .#{$css-prefix}form-split {
    color: $text-color;
  }
  // 
  .#{$css-prefix}input {
    &,
    &:hover {
      border-color: $border-color;
    }

    &:focus {
      @include active($border-color);
    }

    &:not([disabled]):hover {
      border-color: $border-color;
    }
  }

  .#{$css-prefix}calendar-picker-open .#{$css-prefix}calendar-picker-input {
    @include active($border-color);
  }

  .#{$css-prefix}input-group-addon {
    color: $text-color;
    border-color: $border-color;
    background-color: $background-color;
  }

  .has-feedback {
    color: $text-color;
  }
}

// Reset form styles
// -----------------------------
// Based on Bootstrap framework
@mixin reset-form() {
  fieldset {
    padding: 0;
    margin: 0;
    border: 0;
    min-width: 0;
  }

  legend {
    display: block;
    width: 100%;
    padding: 0;
    margin-bottom: 20px;
    font-size: $font-size-lg;
    line-height: inherit;
    color: $text-color-secondary;
    border: 0;
    border-bottom: 1px solid $border-color-base;
  }

  label {
    font-size: $font-size-base;
  }

  input[type="search"] {
    box-sizing: border-box;
  }

  // Position radios and checkboxes better
  input[type="radio"],
  input[type="checkbox"] {
    line-height: normal;
  }

  input[type="file"] {
    display: block;
  }

  // Make range inputs behave like textual form controls
  input[type="range"] {
    display: block;
    width: 100%;
  }

  // Make multiple select elements height not fixed
  select[multiple],
  select[size] {
    height: auto;
  }

  // Focus for file, radio, and checkbox
  input[type="file"]:focus,
  input[type="radio"]:focus,
  input[type="checkbox"]:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color; // lesshint spaceAroundOperator: false
    outline-offset: -2px;
  }

  // Adjust output element
  output {
    display: block;
    padding-top: 15px;
    font-size: $font-size-base;
    line-height: $line-height-base;
    color: $input-color;
  }
}



