@import "../../style/config";
@import "../../style/mixins";

$cascader-prefix-cls: "#{$css-prefix}cascader";

.#{$cascader-prefix-cls} {
  font-size: $font-size-base;
  &-input.#{$css-prefix}input {
    background-color: transparent;
    display: block;
    cursor: pointer;
    width: 100%;
    z-index: 1;
  }
  &-picker {
    position: relative;
    display: inline-block;
    cursor: pointer;
    vertical-align: middle;
    font-size: $font-size-base;
    background-color: #fff;
    border-radius: $border-radius-base;

    &-with-value &-label {
      color: transparent;
    }

    &-disabled {
      cursor: not-allowed;

      .#{$cascader-prefix-cls}-input {
        cursor: not-allowed;
      }
    }

    &-label {
      position: absolute;
      left: 0;
      height: 20px;
      line-height: 20px;
      top: 50%;
      margin-top: -10px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 100%;
      padding: 0 12px 0 8px;
    }

    &-clear {
      opacity: 0;
      position: absolute;
      right: 8px;
      z-index: 2;
      background: #fff;
      top: 50%;
      font-size: $font-size-base;
      color: #ccc;
      width: 12px;
      height: 12px;
      margin-top: -6px;
      line-height: 12px;
      cursor: pointer;
      transition: color 0.3s ease, opacity 0.15s ease;
      &:hover {
        color: $text-color-secondary;
      }
    }

    &:hover &-clear {
      opacity: 1;
    }

    // arrow
    &-arrow {
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 8px;
      width: 12px;
      height: 12px;
      margin-top: -6px;
      line-height: 12px;
      color: $text-color-secondary;
      @include iconfont-size-under-12px(9px);
      &:before {
        transition: transform 0.2s ease;
      }
      &-expand {
        &:before {
          transform: rotate(180deg);
        }
      }
    }
  }
  &-menus {
    font-size: $font-size-base;
    background: #fff;
    position: absolute;
    z-index: $zindex-dropdown;
    border-radius: $border-radius-base;
    box-shadow: $box-shadow-base;
    white-space: nowrap;
    &-empty,
    &-hidden {
      display: none;
    }
    &.slide-up-enter.slide-up-enter-active.#{$cascader-prefix-cls}-menus-placement-bottomLeft,
    &.slide-up-appear.slide-up-appear-active.#{$cascader-prefix-cls}-menus-placement-bottomLeft {
      animation-name: antSlideUpIn;
    }

    &.slide-up-enter.slide-up-enter-active.#{$cascader-prefix-cls}-menus-placement-topLeft,
    &.slide-up-appear.slide-up-appear-active.#{$cascader-prefix-cls}-menus-placement-topLeft {
      animation-name: antSlideDownIn;
    }

    &.slide-up-leave.slide-up-leave-active.#{$cascader-prefix-cls}-menus-placement-bottomLeft {
      animation-name: antSlideUpOut;
    }

    &.slide-up-leave.slide-up-leave-active.#{$cascader-prefix-cls}-menus-placement-topLeft {
      animation-name: antSlideDownOut;
    }
  }
  &-menu {
    display: inline-block;
    vertical-align: top;
    min-width: 111px;
    height: 180px;
    list-style: none;
    margin: 0;
    padding: 0;
    border-right: 1px solid $border-color-split;
    overflow: auto;
    &:first-child {
      border-radius: $border-radius-base 0 0 $border-radius-base;
    }
    &:last-child {
      border-right-color: transparent;
      margin-right: -1px;
      border-radius: 0 $border-radius-base $border-radius-base 0;
    }
    &:only-child {
      border-radius: $border-radius-base;
    }
  }
  &-menu-item {
    padding: 7px 26px 7px 16px;
    cursor: pointer;
    white-space: nowrap;
    transition: all 0.3s ease;
    &:hover {
      background: tint($primary-color, 90%);
    }
    &-disabled {
      cursor: not-allowed;
      color: #ccc;
      &:hover {
        background: transparent;
      }
    }
    &-active:not(&-disabled) {
      &,
      &:hover {
        background-color: $background-color-base;
        font-weight: bold;
      }
    }
    &-expand {
      position: relative;
      &:after {
        @include iconfont-font("e61f");
        @include iconfont-size-under-12px(8px);
        color: $text-color-secondary;
        position: absolute;
        right: 15px;
      }
    }

    & &-keyword {
      color: $highlight-color;
    }
  }
}



