@import "../../style/config";

$message-prefix-cls: "#{$css-prefix}message";

.#{$message-prefix-cls} {
  font-size: $font-size-base;
  position: fixed;
  z-index: $zindex-message;
  width: 100%;
  top: 16px;
  left: 0;

  &-notice {
    width: auto;
    vertical-align: middle;
    position: absolute;
    left: 50%;
  }

  &-notice-content {
    position: relative;
    right: 50%;
    padding: 8px 16px;
    border-radius: $border-radius-base;
    box-shadow: $shadow-2;
    background: #fff;
    display: block;
  }

  &-success .#{$iconfont-css-prefix} {
    color: $success-color;
  }

  &-error .#{$iconfont-css-prefix} {
    color: $error-color;
  }

  &-warning .#{$iconfont-css-prefix} {
    color: $warning-color;
  }

  &-info .#{$iconfont-css-prefix},
  &-loading .#{$iconfont-css-prefix} {
    color: $primary-color;
  }

  .#{$iconfont-css-prefix} {
    margin-right: 8px;
    font-size: $font-size-lg;
    top: 1px;
    position: relative;
  }
}



